* {
  box-sizing: border-box;
  margin: auto;
  padding: 0;
}



a {
  text-decoration: none;
}

.App {
  text-align: center;
  
  margin: auto;
}
.Myfoot
{
  text-align: center;
  font-size: 20px;
  color: green;
  font-family: cursive;
}
body{
  background: #000 url('images/bg.jpg') no-repeat center center/cover;
  height: 100vh;
 
}
.search
{
  height: 100px;
  margin-top: 20px;
  
}
input[type='text'] {
  display: block;
  padding: 10px;
  font-size: 20px;
  border: 0;
  border-radius: 5px;
  width: 50%;
  margin: auto;
  outline: none;
}
.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin:5%;
  row-gap: 5vh;

}

.card {
  cursor: pointer;
  background-color: transparent;
  height: 300px;
  object-fit: cover;
  width: 80%;
  perspective: 1000px;
}

.card h1 {
  font-size: 25px;
  border-bottom: 1px #fff solid;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.card img {
  width: 100%;
  height: 300px;
  object-fit: fill;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.card-back {
  background-color: #333;
  color: white;
  padding: 20px;
  transform: rotateY(180deg);
}

.card li {
  list-style: none;
  padding-bottom: 10px;
}
@media (max-width: 800px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
 
}

@media (max-width: 500px) {
  .cards {
    grid-template-columns: 1fr;
    padding: 10px;
  }
  .card img {
    width: 100%;
    height: 300px;
    object-fit: fill;
  }
  .bolt
  {
    
    width: 30vh;
  }
  input[type='text'] {
    display: block;
    padding: 10px;
    font-size: 20px;
    border: 0;
    border-radius: 5px;
    width: 80%;
    margin: auto;
    outline: none;
}
}
